export const email = {
  rules: [
    {
      test: /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/i,
      message: "Informe um e-mail vlido."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};

export const celular = {
  rules: [
    {
      test: /^(?:\+)[0-9]{2}\s?(?:\()[0-9]{2}(?:\))\s?[0-9]{4,5}(?:-)[0-9]{4}$/i,
      message: "Informe um nmero vlido."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};

export const url = {
  rules: [
    {
      test: /^(ftp|http|https):\/\/[^ "]+$/i,
      message: "Informe uma url vlida."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};
export const cnpj = {
  rules: [
    {
      test: /[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2}/g,
      message: "Informe um CNPJ vlido."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};
export const cpf = {
  rules: [
    {
      test: /^[0-9]{3}.?[0-9]{3}.?[0-9]{3}-?[0-9]{2}/,
      message: "Informe um CPF vlido."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};


export const password = {
  rules: [
    {
      test: /^(?=\w{5})(?=.*\d{2,})/i,
      message: "A senha precisa ser maior do que 6 caractres."
    },
    {
      test: /^[a-z0-9A-Z_]+$/,
      message: "Informe uma senha vlida."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};
export const username = {
  rules: [
    {
      test: /^[a-zA-Z_]+$/i,
      message: "Nmeros no so permitidos."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};
export const string5to30 = {
  rules: [
    {
      test: /^([a-zA-Z0-9_-]){3,30}$/,
      message: "Precisa ter entre 3 e 30 caractres."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};





